
@charset "UTF-8";
@import url(./assets/iconfont/iconfont.css);
.halo-tree * {
  font-size: 13px;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
}
.halo-tree .root {
  position: relative;
  left: -19px;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 14px;
}
.halo-tree .root i {
    font-size: 14px;
    cursor: pointer;
}
.halo-tree .input {
  width: 100%;
  position: relative;
}
.halo-tree .input span {
  position: absolute;
  top: 7px;
  right: 5px;
}
.halo-tree .input input {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 5px;
  height: 25px;
  margin-top: 2px;
}
.halo-tree .input input:focus {
  border: none;
}
.halo-tree .search {
  width: 14px;
  height: 14px;
  background-image: url("assets/search.png");
}



