
.halo-tree *{
  font-size: 13px;
  font-family: '\5FAE\8F6F\96C5\9ED1'
}
.halo-tree .input{
  width:100%;
  position: relative;
}
.halo-tree .input span {
  position: absolute;
  top: 9px;
  right: 10px;
}
.halo-tree .input input{
  display: inline-block;
  box-sizing: border-box;
  width:100%;
  border-radius: 5px;
  height:25px;
  padding-left: 5px;
  border:transparent;
  border:1px solid #ddd;
}
.halo-tree .input input:focus {
    border:none;
}



